@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Heebo:wght@300;400&display=swap");
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0px;
  padding: 0px;
  font-weight: 300;
  display: flex;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  line-height: 1.1;
  margin: 0;
}

h1 {
  font-size: clamp(1.875rem, 8vw, 5rem);
}

h2 {
  font-size: clamp(1.40625rem, 6vw, 3.75rem);
}

h3 {
  font-size: clamp(1.171875rem, 5vw, 3.125rem);
}

h4 {
  font-size: clamp(0.703125rem, 3vw, 1.875rem);
}

x h5 {
  font-size: clamp(0.5859375rem, 2vw, 1.5625rem);
}

h6 {
  font-size: clamp(0.625rem, 2vw, 1.25rem);
}

p {
  font-size: clamp(0.625rem, 2vw, 1.25rem);
}

p {
  margin: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --side-bg:#F3E2EC;
  --side-blue:#0015D9;
  --pink:#D95887;
  --gray: rgb(134, 134, 134);
  --dark-blue:#2B3E7E;
  --light-blue:#489BD3;
  --light-gray:rgb(230, 230, 230);
  --dark-gray:rgb(34, 34, 34);
  --true-pink:#ec3477;
  --white:#fff;
  --dark-pink:rgb(211, 0, 35);
  --input-border:#ECECED;
  --input-bg:#FAFAFB;
  --hr:#E1E1E4;
}

aside .text {
  padding: 3.125rem;
}
aside .main {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  width: clamp(400px, 27vw, 600px);
  background-color: var(--side-bg);
  font-family: "Roboto", sans-serif;
  color: var(--side-blue);
}
aside .main h1 {
  font-size: 2.1875rem;
  font-weight: 900;
}
aside .main p {
  margin-top: 0.625rem;
  line-height: 1.3;
  font-family: "Heebo", sans-serif;
  font-weight: 400;
  font-size: 1.375rem;
  margin-bottom: 30px;
}
aside .main .user {
  display: flex;
  align-items: center;
}
aside .main .user img {
  width: 50px;
  border-radius: 50%;
}
aside .main .user div {
  margin-left: 20px;
}
aside .main .user div .name {
  font-size: 1rem;
  margin-bottom: 7px;
}
aside .main .user div .post {
  font-size: 1rem;
  font-weight: 400;
}
aside .main .btm-art {
  width: 200%;
}
@media only screen and (max-width: 850px) {
  aside .main {
    display: none;
  }
}

main {
  width: 100vw;
  height: 100vh;
  overflow-y: scroll;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Roboto", sans-serif;
}
main .payment-details {
  width: clamp(200px, 35vw, 700px);
}
main .payment-details h1 {
  font-size: 1.875rem;
  font-weight: 900;
  margin-bottom: 30px;
  margin-top: 30px;
}
main .payment-details .methord input[type=radio] {
  display: none;
}
main .payment-details .methord input[type=radio]:checked + label::before {
  border: 5px solid var(--pink);
}
main .payment-details .methord label {
  position: relative;
  margin: 0px 40px 0px 40px;
  font-weight: 400;
}
main .payment-details .methord label .dark-blue, main .payment-details .methord label .light-blue {
  color: var(--dark-blue);
  font-weight: 900;
  font-size: 1.25rem;
  font-style: italic;
}
main .payment-details .methord label .light-blue {
  color: var(--light-blue);
}
main .payment-details .methord label::before {
  content: "";
  width: 20px;
  height: 20px;
  border: 1px solid var(--gray);
  border-radius: 50%;
  position: absolute;
  top: -1px;
  left: -30px;
}
main .card-details {
  margin-top: 30px;
}
main .card-details input {
  padding: 0.625rem;
  border-radius: 10px;
  border: 2px solid var(--input-border);
  background-color: var(--input-bg);
  color: var(--gray);
  font-size: 1.25rem;
  margin-bottom: 15px;
  width: 100%;
}
main .card-details input:focus {
  outline: none !important;
  border: 2px solid var(--input-border);
  box-shadow: 0 0 10px var(--side-bg);
}
main .card-details label {
  font-weight: 700;
  margin-bottom: 10px;
}
main .card-details .card-no, main .card-details .exp-date, main .card-details .cvv {
  display: flex;
  flex-direction: column;
}
main .card-details .card-other {
  display: flex;
  justify-content: space-between;
}
main .card-details .exp-date {
  width: 65%;
}
main .card-details .cvv {
  width: 30%;
}
main .line {
  height: 1px;
  background-color: var(--hr);
  margin-top: 40px;
  border: none;
}
main .payment-info .payment-numbers div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 15px 0px 15px 0px;
}
main .payment-info .payment-numbers div h1 {
  font-size: 1.0625rem;
  margin: 0;
  font-weight: 400;
}
main .payment-info .payment-numbers div .billed-now {
  font-weight: 700;
  font-size: 1.1875rem;
}
main .payment-info .payment-numbers div h2 {
  background-color: var(--dark-gray);
  font-size: 0.9375rem;
  padding: 5px;
  border-radius: 5px;
  color: var(--light-gray);
  margin-right: 10px;
}
main .payment-info .payment-numbers div .discount {
  color: var(--true-pink);
}
main .details p {
  font-size: 0.875rem;
  color: var(--dark-gray);
  text-align: justify;
  line-height: 1.4;
}
main .submit {
  color: var(--white);
  background-color: var(--true-pink);
  padding: 20px;
  width: clamp(170px, 35%, 45%);
  border: none;
  border-radius: 10px;
  font-size: 1.125rem;
  margin-top: 25px;
  margin-bottom: 30px;
  cursor: pointer;
  transition-duration: 0.5s;
}
main .submit:hover {
  background-color: var(--dark-pink);
}
@media only screen and (max-width: 850px) {
  main .payment-details {
    width: 85%;
    margin: auto;
  }
}